import React, {useEffect, useState } from "react";
import { useSelector } from "react-redux";
import { Route ,Redirect , Switch } from "react-router-dom";
import "./App.css";
import Header from "./Components/Header/Header";
import Home from "./Components/Home/Home";
import Post from "./Components/Home/Post/Post";
import MainPage from "./Components/MainScreen/MainPage";
import { sendUpdate } from "./Hooks/api";
import useHTTP from "./Hooks/use-http";
let initial = true;
function App() {
  const items = useSelector(state => state.items);
  const userId = useSelector(state => state.userId);
  const changed = useSelector(state => state.changed);
  const {sendRequest} = useHTTP(sendUpdate)
  const val = localStorage.getItem("isLoggedIn") === "1"; 
  const [isLoggedIn, setIsLoggedIn] = useState(val);
  const [showPost, setshowPost] = useState(false);
  const LoginHandler = () => {
    setIsLoggedIn(true);
  };
  useEffect(() => {
     if(initial) {
       initial = false;
     }
     else if(isLoggedIn && changed) {

       sendRequest({items : items , userId : userId})
     }
  },[isLoggedIn , items , sendRequest  , changed , userId] );

  const LogoutHandler = () => {
    localStorage.setItem("isLoggedIn" , "0");
    setIsLoggedIn(false);
    setshowPost(false);
  };
  return (
    <> 
      <Header
        ShowSignIn ={false}
        ShowSignUp ={false}
        onShowPost={showPost}
        onLogout={LogoutHandler}
        isLoggedIn={isLoggedIn}
      />
      <Switch>
      <Route path="/" exact>
      {isLoggedIn && <Redirect to="/home"/>}
      {!isLoggedIn && <Redirect to="/main"/>}
      </Route>
      <Route path="/home/post" exact>
      {isLoggedIn &&  <Post />}
      {!isLoggedIn && <Redirect to="/main"/>}
      </Route>
      <Route path="/home" exact>
      {isLoggedIn && <Home/>}
      {!isLoggedIn && <Redirect to="/main"/>}
      </Route>
      <Route path="/main" exact>
      {<MainPage onSignIn={LoginHandler} />}
      </Route>
      <Route path="*">
       <Redirect to="/main"/>
      </Route>
      </Switch>
      </>
  );
}

export default App;
